export interface OWspTrashInit extends OSkinableInit {
	reg?: IReg<IWspUiEnv>

	/** Affiche le panel de la mainView. */
	showMainView?: boolean

	/** Node à sélectionner à l'init. */
	selectLiveSrcUri?: srcUri
}

export interface WspTrash extends BaseElementAsync, IView {
	initialize(init: OWspTrashInit): this
}

export class WspTrash extends BaseElementAsync implements IView, IViewsContainer, IGridCategorizer<JSrcFields, JSrcFieldsTree>, IShortDescCtx {

	reg: IReg<IWspUiEnv>;

	maxEntries = 20_000;

	_grid: SrcGrid;

	_treedatas: WspTrashTreeDatas;

	critDirty: boolean = false;
	spaceRoot: srcUri = SRC.URI_ROOT;

	protected _appHeader: AppHeader<WspTrash>;
	protected _spaceRootBtn: Button;
	protected _removedBy: InputUserPanel<any>;
	protected _inputTxt: HTMLInputElement;
	protected _inputPeriod: InputPeriod;
	protected _inputRecursive: HTMLInputElement;

	protected _searchBtn: Button;

	protected _mainView: ItemViewerSingle;

	protected _srcUriToSelOnInit: srcId;

	protected _lastError: any;

	protected _fetching: Promise<JSrcFields[]>;

	get wsp(): Wsp {return this.reg.env.wsp};

	place: WspsLivePlace;

	infoBroker: IInfoBroker;

	get shortDescs(): JSrcFields[] {return this._grid.shortDescs};

	get emitter() {return this};

	setSpaceRoot(root: srcUri) {
		if (!root) root = SRC.URI_ROOT;
		if (this.spaceRoot != root) {
			this.spaceRoot = root;
			this._spaceRootBtn.label = ITEM.extractSpaceLabel(this.spaceRoot);
			this.setCritDirty();
		}
	}

	setCritDirty() {
		if (!this.critDirty) {
			this.critDirty = true;
			this.resetList();
		}
	}

	protected async _initialize(init: OWspTrashInit): Promise<void> {
		this.reg = REG.createSubReg(this.findReg(init));
		this._initReg(init);

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("webzone:panel", sr);
		this._initAndInstallSkin(this.localName, init);
		this.infoBroker = this.reg.env.infoBroker;
		this._spaceRootBtn = <Button id="spaceRoot" î={{
			reg: this.reg,
			label: ITEM.extractSpaceLabel(SRC.URI_ROOT),
			uiContext: "dialog",
			title: "Sélectionner un espace..."
		} as OButtonInit} onclick={this.onChooseSpace}/> as Button;

		this._removedBy = new InputUserPanel().initialize({
			required: false,
			reg: this.reg,
			emptySelectionMsg: "Choisir un compte...",
			userCard: "multi",
			usersGridInit: {
				usersSrv: this.reg.env.universe.useUsers,
				grid: {selType: "multi"},
				filterType: EUserType.user,
				filterTypeInputVisibility: true,
			},
		} as OInputUserInit<any>);
		await this._removedBy.initializedAsync;

		this._inputTxt = <input id="codeFilter" type="search" spellcheck="false"/> as HTMLInputElement;
		this._inputPeriod = new InputPeriod();
		this._inputPeriod.title = "Période dans laquelle les contenus ont été mis en corbeille";
		this._inputRecursive = <input id="recursive" type="checkbox"/> as HTMLInputElement;

		this._searchBtn = <Button id="search" î={{reg: this.reg, label: "Rechercher", uiContext: "dialog"} as OButtonInit} onclick={this.doSearch.bind(this)}/> as Button;

		const gridDatas = new SrcGridDatas();
		gridDatas.useLiveSrcUri = true;

		this._grid = new WspTrashGrid().initialize({
			reg: this.reg,
			itemHandlingReact: this.infoBroker,
			actions: this.reg.getList("actions:wspTrash:shortDesc"),
			/* defaultAction: infoBroker ? new SpaceTreeFocusItem() : null, */
			accelKeyMgr: new AccelKeyMgr().initFromMapActions(this.reg.getListAsMap("accelkeys:wspTrash:shortDesc")),
			hideHeaders: true,
			skinOver: "wsp-src-grid",
			skinScroll: "scroll/small",
			dataHolder: gridDatas,
			emptyBody: () => {
				if (this.critDirty) return this._searchBtn;
				if (this._fetching || this._waitingExec) return <c-msg label="Recherche dans la corbeille..." level="info"/>;
				if (this._lastError) return <c-msg label="Erreur au chargement" level="error"/>;
				return <c-msg label="Aucun contenu en corbeille trouvé" level="info"/>;
			}
		});

		this._treedatas = new WspTrashTreeDatas(this.wsp, this.wsp.getShortDescFields(), gridDatas);
		this._grid.setCategorizer(this, this._treedatas);
		this._grid.setAttribute("c-resizable", "");

		this._appHeader = sr.appendChild(new AppHeader<WspTrash>().initialize({
			reg: this.reg,
			focusListening: this._grid,
			actionContext: this,
			forceSharedBar: true
		}));

		sr.appendChild(<div id="crit" onchange={this._onCritChange} oninput={this._onCritChange}>
			<label><span>Espace de recherche</span>{this._spaceRootBtn}</label>
			<label><span>Filtre sur le code</span>{this._inputTxt} <span>Supprimé par</span>{this._removedBy}</label>
			<label><span>Date de mise en corbeille</span>{this._inputPeriod} {this._inputRecursive}<span>Recherche dans les espaces supprimés</span></label>
		</div>);

		if (init.selectLiveSrcUri) {
			this._inputTxt.value = init.selectLiveSrcUri;
			this._inputRecursive.checked = true;
			this._srcUriToSelOnInit = init.selectLiveSrcUri;
		}

		this._fetchRootDatas(); //avant appendChild(this._grid); pour init après lancement de la requete -> emptyBody ok.

		if (init.showMainView) {
			const parentMainView = <div id="srcView" c-resizable=""/>;
			sr.appendChild(<div id="main">
				{this._grid}
				<c-resizer c-orient="row"/>
				{parentMainView}
			</div>);
			this._mainView = new ItemViewerSingle();
			await this._mainView.initViewer(this.reg, parentMainView, null, null);
			this.addEventListener('grid-select', this._onGridSelect);
		} else {
			sr.appendChild(this._grid);
		}
	}

	protected _initReg(init: OWspTrashInit) {
		const wspFrom = this.reg.env.wsp;
		const wsp = this.reg.env.wsp = new Wsp(wspFrom.wspServer, wspFrom.code, wspFrom.infoWsp, wspFrom.wspMetaUi);
		this._initWspReg(wsp);
		if (init.showMainView) {
			//InfoBroker local à cette fenetre de dialogue.
			this.reg.env.infoBroker = new InfoBrokerBasic();
		}

		//Suppr de l'action de copier.
		this.reg.addToList("actions:item:burger", "copy", 2, null);
		//Ajout de l'action de restore sur l'item
		this.reg.addToList("actions:item:burger", "restoreTo", 1, new RestoreToAction());
	}

	protected _initWspReg(wsp: Wsp) {
		//Ajout des champs nécessaires à la gestion de la corbeille
		wsp.reg.addToList('srcFields:shortDesc', 'srcLiveUri', 1, 'srcLiveUri');
		wsp.reg.addToList('srcFields:shortDesc', 'srcTrashed', 1, 'srcTrashed');
		if (wsp.wspDefProps.drvAxis) {
			//Pour différencier un contenu erased qui est propre à l'atelier (drvAxisDefCo===null) : il peut alors être supprimé définitivement.
			wsp.reg.addToList('srcFields:shortDesc', 'drvAxisDefCo', 1, 'drvAxisDefCo');
		}
	}

	visitViews(visitor: (view: IViewApi) => any, options?: OViewVisitOptions): any {
		return VIEWS.visitNodes(visitor, options, this._mainView);
	}

	visitViewsAsync(visitor: (view: IViewApi) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		return VIEWS.visitNodesAsync(visitor, options, this._mainView);
	}

	onViewShown() {
		if (!this.place) {
			this.reg.env.wsp.listenChanges();
			this.place = this.reg.env.place = this.reg.env.wsp.newPlaceWsp();
			this.place.eventsMgr.on("onConnectionLost", () => {this._appHeader.setHeadband("Votre connexion au serveur est interrompue ou instable.", "error")})
			this.place.eventsMgr.on("onConnectionRenewed", () => {this._appHeader.setHeadband(null)});
			this.place.eventsMgr.on("wspLiveStateChange", this.onWspLiveStateChange.bind(this));
		}
		VIEWS.onViewShown(this._mainView);
	}

	onViewHidden(closed?: boolean) {
		if (closed && this.place) {
			this.reg.env.wsp.stopListenChanges();
			this.place.closePlace();
			this.place = null;
		}
		VIEWS.onViewHidden(this._mainView);
	}

	msgOver: MsgOver;

	protected onWspLiveStateChange(wsp: Wsp) {
		if (wsp !== this.wsp) return;
		if (wsp.isAvailable) {
			this._initWspReg(wsp); //surcharge du reg du wsp après son rechargement.
			if (this.msgOver) {
				this.msgOver.removeMsg();
				this.msgOver = null;
			}
			this.resetList();
			this._fetchRootDatas();
		} else if (wsp.isInError) {
			POPUP.findPopupableParent(this).close();
		} else if (!this.msgOver) {
			this.msgOver = new MsgOver().showMsgOver(this).setCustomMsg("Reconfiguration de l'atelier en cours...");
		}
	}

	fetchRootDatas() {
		return this._fetchRootDatas();
	}

	protected async _fetchRootDatas() {
		if (WSPTRASH_DEBUG) console.log("WspTrash.fetchRootDatas:::start");
		// if (this.callbackSearching) this.callbackSearching.onSearching('searchInit'); pas de searching
		this._lastError = undefined;
		try {
			const requ = this._getRequest();
			if (WSPTRASH_DEBUG) console.log("WspTrash.fetchRootDatas:::request", requ.where.toDom());
			this._fetching = WSP.fetchSearchJson<JSrcFields>(this.wsp, this._grid, requ.toXml());
			const datas = await this._fetching; //this.filterNotTrashedRes(await this._fetching);
			this._fetching = null;
			this._lastError = null;
			if (WSPTRASH_DEBUG) console.log("WspTrash.fetchRootDatas:::datas", datas);
			if (datas.length > this.maxEntries) {
				const maxEntries = this.maxEntries;
				POPUP.showNotifInfo(`Plus de ${maxEntries} éléments trouvés en corbeille. Utilisez les critères de recherche pour restreindre la liste.`, this);
			}
			this._lastError = null;
			this._grid.srcGridDatas.setDatas(datas);
			if (this._srcUriToSelOnInit) {
				const srcUri = this._srcUriToSelOnInit;
				this._srcUriToSelOnInit = null;
				const rowKey = this._grid.srcGridDatas.findRowKeyBySrcUri(srcUri);
				if (rowKey) this._grid.setSelectedRows(this._grid.srcGridDatas.getOffset(rowKey));
			}
		} catch (e) {
			this._fetching = null;
			this._lastError = e;
			console.log(e);
			this._grid.srcGridDatas.setDatas([]);
		} finally {
			//if (this.callbackSearching) this.callbackSearching.onSearching('searchEnd');
			if (WSPTRASH_DEBUG) console.log("WspTrash.fetchRootDatas:::end");
		}
	}

	// protected filterNotTrashedRes(datas: JSrcFields[]): JSrcFields[] {
	// 	if (!datas) return [];
	// 	//On masque les items de type Res non trashed mais qui peuvent être retournés car un de ses flux est en corbeille.
	// 	const wspMetaUi = this.wsp.wspMetaUi;
	// 	return datas.filter((src) => !(!src.srcTrashed && wspMetaUi.getItemType(src.itModel).getFamily() === EItemTypeFamily.res));
	// }


	protected doSearch() {
		this.critDirty = false;
		if (this._waitingExec) return;
		this._fetchRootDatas();
	}

	protected resetList() {
		this._lastError = null;
		this._grid.srcGridDatas.setDatas([]);
	}

	protected _getRequest(): SearchRequest {
		const where = new SearchNodesInTrash(this.spaceRoot, -1, this._inputRecursive.checked, null,
			new SearchItemSrcType(ESearchItemSrcTypes.SPACE | ESearchItemSrcTypes.ITEMFOLDER | ESearchItemSrcTypes.ITEMFILE),
			this._removedBy.value?.length ? new SearchLastUser(this._removedBy.value) : undefined,
			this._inputTxt.value ? new SearchRegexpUri(SpaceTreeReqUri.buildSrcUriPattern(this._inputTxt.value)) : undefined,
			SearchLastModif.buildFromInputPeriod(this._inputPeriod)
		);
		return new SearchRequest(this.wsp.getShortDescDef()).setWhere(where).setMax(this.maxEntries + 1);
	}

	protected _onCritChange(this: HTMLElement) {
		DOMSH.findHost<WspTrash>(this).setCritDirty();
	}

	async onChooseSpace(this: Button, ev: Event) {
		const wspTrash = DOMSH.findHost<WspTrash>(this);
		const {SpaceSelector} = await import("back/wsp/dialogs/spaceSelector.js");
		const ct = new SpaceSelector().initialize({
			reg: wspTrash.reg,
			startSel: wspTrash.spaceRoot,
			spaceTree: {
				showRoot: true
			},
			selOnDblClick: true
		});
		const space = await POPUP.showMenuFromEvent<JSrcFields | undefined>(ct, ev, wspTrash).onNextClose();
		if (space) wspTrash.setSpaceRoot(space.srcUri);
	}

	protected _waitingExec: number;
	protected _lastTxt: string;
	protected _lastMoreThan: number | null;
	protected _lastLessThan: number | null;


	protected _onGridSelect(ev: Event) {
		if (this._showTimer) clearTimeout(this._showTimer);
		const count = this._grid.countSelectedRows();
		if (count === 0) {
			this.reg.env.infoBroker.dispatchInfo(new InfoFocusItem(null), this);
		} else if (count === 1) {
			this._showTimer = setTimeout(() => {
				let sd = this._grid.shortDescs[0];
				if (sd) {
					switch (ITEM.getSrcUriType(sd.srcLiveUri || sd.srcUri)) {
					case "space":
						sd = null;
						break;
					case "res":
						sd = this._treedatas.getRowKeyParent(sd);
						while (ITEM.getSrcUriType(sd.srcUri) !== "item") sd = this._treedatas.getRowKeyParent(sd);
					}
					if (sd && (!this._mainView.longDesc || this._mainView.longDesc.srcUri !== sd.srcUri)) {
						this.reg.env.infoBroker.dispatchInfo(new InfoFocusItem(sd.srcUri, sd), this);
					}
				}
			}, 500);
		}
	}

	protected _showTimer: any;


	getId(data: JSrcFields): any {
		return data.srcLiveUri || data.srcUri;
	}

	getAncestorIds(data: JSrcFieldsTree): any[] | null {
		let parent = SRC.extractUriParent(data.srcLiveUri || data.srcUri);
		if (!parent) return null;
		const stack: any[] = [];
		while (parent) {
			stack.push(parent);
			parent = SRC.extractUriParent(parent);
		}
		stack.reverse();
		return stack;
	}

	wrapForTree(data: JSrcFields): JSrcFieldsTree {
		const e = Object.assign(Object.create(null), data) as JSrcFieldsTree;
		//Déclaration des items de type dossier et des espaces en objets dossiers dans l'arbre.
		if (this.wsp.wspMetaUi.getItemType(data.itModel).getFamily() === EItemTypeFamily.res) {
			//On est sur une res, seul l'item est visible (pas le stream, les  props ou les metas).
			if (ITEM.getSrcUriType(data.srcUri) === "res") return null;
		} else if (!e.ch) {
			if (data.srcSt === ESrcSt.folder) {
				//dossier potentiel
				e.ch = null;
			}
		}
		//Hack des srcRi :
		if (ITEM.isTrashUri(data.srcUri)) {
			//Droits pour "Suppression définitive" et "Restaurer vers..."
			e.srcRi = ESrcRights.READS | ESrcRights.remove | ESrcRights.removeChildren | ESrcRights.move;
		} else {
			//Droits pour "Restaurer vers..." d'un children
			e.srcRi = ESrcRights.READS | ESrcRights.removeChildren;
		}
		return e;
	}

	getFolder(id: any): JSrcFieldsTree {
		//Impl pour tests, ne devrait jamais être appelé...
		//Espace ou item racine de type dossier ancêtre d'un élément supprimé, créé à la volée.
		switch (ITEM.getSrcUriType(id)) {
		case "space":
			return {srcUri: id};
		case "item":
		case "res":
			return {srcUri: id, itModel: "sc_undef"};
		}
		return null;
	}

	shouldRemoveEmptyFolder(folder: JSrcFieldsTree): boolean {
		return false;
	}
}

