/**
 *
 */
class WspTrashGrid extends SrcGrid {

	dataHolder: WspTrashTreeDatas;

	get wspTrash(): WspTrash {return DOMSH.findHost<WspTrash>(this)}

	getDefaultColumnDefs(): GridColDef[] {
		return [new GridColTreeDef('srcTree')
			.setDefaultSort(1, 'ascendant')
			.setFlex('1rem', 1, 1).setMinWidth('55px').setSortable(true)
			.setCellBuilder(new CellBuilderSrcIconCode(this.reg, this.wsp.wspMetaUi, false, this.wsp.srcUriItemsSortFn))];
	}

	redrawLine(row: IGridDataRow, line: HTMLElement) {
		super.redrawLine(row, line);
		line.classList.toggle('trashed', row.getData<ESrcField>("srcTrashed") === true);
	}

	isParentInTrash(child: JSrcFields): boolean {
		const parent = this.dataHolder.getRowKeyParent(child);
		return parent ? ITEM.isTrashUri(parent.srcUri) : false;
	}

}

REG.reg.registerSkin('wsp-trash-grid', 1, /* language=CSS */ `
	.trashed {
		text-decoration-line: line-through;
		text-decoration-color: var(--fade-color);
	}
`);

customElements.define("wsp-trash-grid", WspTrashGrid);


class WspTrashTreeDatas extends GridDataHolderJsonTree<JSrcFieldsTree> {
	constructor(readonly wsp: Wsp, readonly srcFields: ESrcField[], readonly srcGridDatas: ISrcGridDatas) {
		super('ch');
		this.setDefaultOpenState((d: JSrcFieldsTree) => d.ch != null);
	}

	async openFolderAsync(rowKey: any): Promise<boolean> {
		if (this.ch(rowKey) === null) {
			//Dossier à charger...
			try {
				if (WSPTRASH_DEBUG) console.log("WspTrashTreeDatas.openFolderAsync:::start", rowKey.srcUri);
				if (this.folderSt(rowKey) !== EFolderState.closed) return false;
				this.setFolderSt(rowKey, EFolderState.opening);
				const datas = await WSP.fetchSearchJson<JSrcFields>(this.wsp, this._grid, new SearchRequest().addFieldColumns(...this.srcFields).setWhere(new SearchNodesInTrash(rowKey.srcUri, -1)).toXml());
				if (!this.isRowkeyAlive(rowKey)) return false; //modification de l'arbre entre temps, noeud perdu.
				//if (this.srcFilter) datas = datas.filter(this.srcFilter);
				this.setFolderSt(rowKey, EFolderState.closed);
				if (WSPTRASH_DEBUG) console.log("WspTrashTreeDatas.openFolderAsync::::datas:", datas);
				if (datas.length > 0) {
					this.srcGridDatas.updateDatas(this.srcGridDatas.getDatas().length, 0, ...datas);
				} else {
					this.setCh(rowKey, datas);
				}
				return this.openFolder(rowKey);
			} catch (e) {
				POPUP.showNotifError("Accès au serveur impossible", this.grid);
				this.closeFolder(rowKey);
				throw e;
			} finally {
				if (WSPTRASH_DEBUG) console.log("WspTrashTreeDatas.openFolderAsync:::end", rowKey.srcUri);
			}
			return false;
		}
		return this.openFolder(rowKey);
	}

}


class SearchNodesInTrash implements ISearchCrit {

	filters: ISearchCrit[];

	constructor(public path?: string, public depth?: -1 | 1, public recursiveTrash?: boolean, public afterUri?: string, ...filters: ISearchCrit[]) {
		this.filters = filters;
	}

	toDom(): Element | undefined {
		return <exp type="NodesInTrash" path={this.path} depth={this.depth} recursiveTrash={this.recursiveTrash} afterUri={this.afterUri}>
			{this.filters ? this.filters.map((f) => f ? f.toDom() : undefined) : undefined}
		</exp>;
	}
}


class RestoreAction extends SrcAction<WspTrashGrid> {
	constructor() {
		super('restore');
		this._label = "Restaurer";
		this._group = "edit";
		this.atLeastOne = true;
		this.isNotRemoved = false;
		this._enableSrcPerms = ["restore.history"];
		this._icon = "/@skin@/wsp/actions/wspRestore.svg";
	}

	isEnabled(ctx: WspTrashGrid): boolean {
		if (ctx.shortDescs.some((s) => !s.srcTrashed || ctx.isParentInTrash(s))) return false; //
		return super.isEnabled(ctx);
	}

	async execute(ctx: WspTrashGrid, ev?: Event) {
		const uris = ctx.shortDescs.map((s) => s.srcUri);
		try {
			await WSP.srcRestore(ctx.wsp, ctx, uris);
		} catch (e) {
			ERROR.report("Une erreur est survenue durant la restauration", e);
		}
	}
}

class RestoreToAction extends MoveToSrc<WspTrashGrid> {
	constructor() {
		super('restoreTo');
		this._label = "Restaurer vers...";
		this._titlePopup = "Restaurer vers...";
		this._moveButtonLabel = "Restaurer";
		this.isNotRemoved = false;
		this._enableSrcPerms = ["restore.history", "write.node.moveFrom"]; //XXX perm write.node.moveFrom à hériter ?
		this._icon = "/@skin@/wsp/actions/wspRestoreTo.svg";
	}

	isVisible(ctx: WspTrashGrid): boolean {
		//On masque si un des élements sélectionnés :
		// - n'est pas en corbeille (cela n'a pas de sens)
		// - est une res : TODO il faudrait développer un sélecteur de dossier de res, à la place d'un sélecteur d'espace.
		if (ctx.shortDescs.some((s) => !ITEM.isTrashUri(s.srcUri) || ITEM.getSrcUriType(s.srcUri) === "res")) return false;
		return super.isVisible(ctx);
	}

	isEnabled(ctx: WspTrashGrid): boolean {
		return super.isEnabled(ctx);
	}

	async execute(ctx: WspTrashGrid, ev?: Event) {
		if (ctx.shortDescs.length === 1) {
			this.targetFolder = SRC.extractUriParent(ctx.shortDescs[0].srcLiveUri);
		}
		const srcUris = await super.execute(ctx, ev);
		if (!srcUris) return null;
		const srcsToRestore = (await WSP.fetchSrcs(ctx.wsp, ctx, srcUris, ["srcUri", "srcSt"])).filter((s) => s.srcSt === ESrcSt.none);
		if (srcsToRestore.length > 0) await WSP.srcRestore(ctx.wsp, ctx, srcsToRestore.map((s) => s.srcUri));
		await ctx.wspTrash.fetchRootDatas();
		return srcUris;
	}
}

class DeletePermAction extends SrcAction<IShortDescCtx> {
	constructor() {
		super('deletePerm');
		this._label = "Supprimer définitivement";
		this._group = "del";
		this.atLeastOne = true;
		this._enableSrcPerms = ["deletePermanently.trash"];
		this._icon = "/@skin@/wsp/actions/wspDeletePermanently.svg";
	}

	isEnabled(ctx: WspTrashGrid): boolean {
		const wspDefProps = ctx.reg.env.wsp.wspDefProps;
		if (ctx.shortDescs.some((sd) => {
			if (this.isSrcLayered(wspDefProps, sd)) return true;
			return !ITEM.isTrashUri(sd.srcUri)
		})) return false;
		return super.isEnabled(ctx);
	}

	async execute(ctx: WspTrashGrid, ev?: Event) {
		const count = ctx.shortDescs.length;
		if (await POPUP.confirm(count === 1 ? "Confirmez la suppression définitive." : `Confirmez la suppression définitive de ces ${count} éléments.`,
			ctx, {okLbl: "Supprimer définitivement"})) {
			const uris = ctx.shortDescs.map((s) => s.srcUri);
			try {
				await WSP.srcDeletePermanently(ctx.wsp, ctx, uris);
			} catch (e) {
				ERROR.report("Une erreur est survenue durant la suppression définitive.", e);
			}
		}
	}
}


function addAction(action: IAction<IShortDescCtx>, accel?: string, sortKey?: number) {
	REG.reg.addToList("actions:wspTrash:shortDesc", action.getId(), 1, action, sortKey);
	if (accel) REG.reg.addToList("accelkeys:wspTrash:shortDesc", accel, 1, action);
}

addAction(new RestoreAction());
addAction(new RestoreToAction());
addAction(new DeletePermAction());


const WSPTRASH_DEBUG = false;
